* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  background: #ffffff;
}

.header {
  margin: auto -10px;
  padding: 10px 20px;
  box-shadow: 0 0 2px 0 rgba(114, 114, 114);
  display: flex;
  flex-direction: row;
  align-self: center;
  align-items: center;
}

.header h3 {
  font-size: 1.5rem;
  margin-left: 10px;
  color: #5f6368;
}

form {
  position: relative;
  width: 600px;
  margin: 32px auto 50px auto;
  background: #fff;
  padding: 7px;
  border-radius: 7px;
  box-shadow: 0 1px 7px rgb(128, 128, 128);
}

form input {
  width: 100%;
  border: none;
  padding: 4px 10px;
  margin-bottom: 20px;
  outline: none;
  font-size: 1rem;
  resize: none;
}

form textarea {
  width: 100%;
  border: none;
  padding: 4px 10px;
  margin-bottom: 20px;
  outline: none;
  font-size: 1rem;
  resize: none;
}

form button {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 18px;
  bottom: -18px;
  color: #fff;
  background: #fbbc04;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  cursor: pointer;
  box-shadow: 0 1px 3px rgb(165, 165, 165);
  outline: none;
}

.note {
  background: #fff;
  width: 240px;
  border-radius: 7px;
  box-shadow: 0 2px 5px rgb(175, 175, 175);
  padding: 10px;
  margin: 16px;
  float: left;
  margin-right: 0;
}

.note h1 {
  font-size: 1rem;
  margin-bottom: 6px;
}

.note p {
  font-size: 1rem;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.note button {
  position: relative;
  float: right;
  color: #fbbc04;
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
}

.count {
  display: flex;
  margin-top: 20px;
  justify-content: center;
  align-items: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

@media screen and (max-width: 480px) {
  form {
    padding: 8px;
    width: 80%;
  }
  form input,
  form textarea {
    padding: 6px;
    margin-bottom: 8px;
  }
  form button {
    padding: 6px;
  }
  .note {
    width: 48%;
    margin-right: 2%;
  }
}
